import Button from './Button';
import Arrow from './Arrow';
import './css/Main.css';
import { Link } from 'react-router-dom';
import 'chart.js/auto'
import { Chart } from 'react-chartjs-2'
import { useEffect, useState } from 'react';
import styled from 'styled-components';

//   
function Main() {

    useEffect(() => {
        sessionStorage.clear();
    }, [])

    const [coronas, setCoronas] = useState([]);

    const getCorona = async () => {
        const corona = await fetch(`http://13.58.124.132/corona`);
        const corona_json = await corona.json();
        setCoronas(corona_json);
    }

    console.log(coronas);

    useEffect(() => {
        getCorona()
    }, []);



    return (
        <main>
            <article className='main'>
                <img src="./image/netflix-logo.png" alt=" " className='logo' />
                <h1>   <br /></h1>
                <Link to="/page">
                    <Button text="" />
                </Link>
                <Arrow direction="down" />
            </article>
            <article className='how_to_use1'>
                <p>2020 COVID-19  OTT    .<br />
                         <br />
                       .
                </p>
                <CovidChart getData={coronas} />
                <Arrow direction="down" />
            </article>
            <article className='how_to_use2'>
                <p> 
                    <img src="./image/netflix-logo.png" alt=" " className='logo' />
                       .<br />
                         <br />
                      .
                </p>
                <img src="./image/world-map-movie.png" alt="" className='world_map' />
                <Arrow direction="up" />
            </article>
        </main>
    )
}

export default Main;

//  
function CovidChart({getData}) {

    const coronas = getData;

    const data = {
        labels: coronas.map(x => x.years),
        datasets: [
            {
            type: 'line',
            label: '  ',
            borderColor: '#F05454',
            borderWidth: 2,
            data: coronas.map(x => x.movie_cost / 100000000),
            tension: 0.5,
            yAxisID: 'y0', //  id
            },
            {
            type: 'line',
            label: '  ',
            borderColor: '#30475E',
            borderWidth: 2,
            data: coronas.map(x => x.ott_cost / 100000000),
            tension: 0.5,
            yAxisID: 'y1',
            },
        ],
        };
        
        const options = {
        scales: { //  
            y0: {   //  id   
                id: 'y0',
                type: 'linear',
                display: true,
                position: 'left',
                grid: { //    grid 
                    borderColor: "rgb(54, 162, 235)",
                    },
                title: {
                display: true,
                align: 'center',
                color: 'black',
                font: {
                    size: 14,
                    weight: 300,
                },
                text: '()'
            },
            },
            y1: {
                id: 'y1',
                type: 'linear',
                display: true,
                position: 'right',
                grid: {
                    borderColor: "rgb(255, 99, 132)",
                    },
                title: {
                display: true,
                align: 'center',
                color: 'black',
                font: {
                    size: 14,
                    weight: 300,
                },
                text: '()'
            },
            },
        },
        plugins: {
            legend: { //  
                labels: {
                usePointStyle: true,
                //     , false     .
                },
            },
        }
    };

    const chartAreaPlugin = {
        id: 'chartAreaPlugin',
        beforeDraw(chart, args, options) {
            const { ctx, chartArea : {top, bottom, left, right, width, height} } = chart;
            ctx.save();

            // console.log(left, top, bottom, right, width, height);

            ctx.fillStyle = 'rgba(142, 5, 5, 0.1)';
            // const leftMid = (width / 2) + left // x  
            ctx.fillRect(bottom+(width/4)+(left/2), top, width/4, height);
        }
    };

    return (
        <CoronaDiv>
            <Chart type="line" data={data} options={options} plugins={[chartAreaPlugin]} />
        </CoronaDiv>
    )
}

// styled-components
const CoronaDiv = styled.div`
    width: 700px;

    background-color: white;

    padding: 50px;
    margin: auto;
    box-sizing: border-box;
`;