import Arrow from './Arrow';
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend,
  } from 'chart.js';
import { Radar } from 'react-chartjs-2'
import { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import word_cloud from './image/word_cloud.jpg';
import styled from 'styled-components';

function Kcontents() {

    //  ,  
    const location = useLocation();
    const category = location.state.category;
    const selectClass = location.pathname.slice(-1);

    // API   
    const [totalData, setTotalData] = useState({});

    //    
    const score = Math.floor(Number(totalData.score));
    const award = Math.floor(Number(totalData.award));
    const global = Math.floor(Number(totalData.global));
    const popularity = Math.floor(Number(totalData.popularity));

    //  
    const poster = totalData.poster;
    const imdb = totalData.imdb;

    // () 
    const totalScore = Math.floor(totalData.total_score);
    const totalPercent = Math.floor(totalData.class_numbers / totalData.total_numbers * 100);
    const totalContents = totalData.class_numbers;

    // console.log(poster);

    const getTotal = async () => {
        const APIclass = `${category === "movie" ? "Movie" : "Series"}` + selectClass;
        const APItotal = await fetch(
            `http://13.58.124.132/${category}/k-contents/{class}?class=${APIclass}`);
        const APIjson = await APItotal.json();
        setTotalData(APIjson);
    }

    //         
    useEffect(() => { window.scrollTo(0, 0); }, []);
    // location       
    useEffect(() => { getTotal(); }, [category, selectClass]);

    return (
        <>
            <main>
                <article>
                    <div className='divContainer'>
                        <DivContainer>
                            <PosterDiv>
                                {poster && poster.map((item, index) => {
                                    return (
                                        <img src={item} 
                                            alt={"" + index} 
                                            onClick={
                                                () => { window.open(imdb[index], '_blank'); }
                                            } 
                                        />)
                                    })
                                }
                            </PosterDiv>
                            <TotalP>
                                {selectClass} class   {totalScore}   {totalPercent}%  {totalContents}  .
                            </TotalP>
                            <ScoreContainer>
                                <StarDiv>
                                    <p>SCORE <span>{"".repeat(score)}</span></p>
                                    <p>AWARD <span>{"".repeat(award)}</span></p>
                                    <p>GLOBAL <span>{"".repeat(global)}</span></p>
                                    <p>POPULARITY <span>{"".repeat(popularity)}</span></p>
                                </StarDiv>
                                <TotalChart 
                                    score={score} 
                                    award={award} 
                                    global={global} 
                                    popularity={popularity} />
                            </ScoreContainer>
                        </DivContainer>
                    </div>
                    <Arrow />
                </article>
                <article>
                    <div className='divContainer'>
                        <WordCloudP>
                            A class      !
                        </WordCloudP>
                        <img src={word_cloud} alt="" />
                    </div>
                </article>
            </main>
        </>
    )
}

export default Kcontents;

// 
ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend
  );

function TotalChart({score, award, global, popularity}) {

    const data = {
      labels: ['SCORE', 'AWARD', 'GLOBAL', 'POPULARITY'],
      datasets: [
        {
          label: '',
          borderColor: '#8E0505',
          borderWidth: 2,
          backgroundColor: 'rgba(142, 5, 5, 0.2)',
          data: [score, award, global, popularity],
        },
      ],
    };

    const options = {
        maintainAspectRatio: false, //       ?
        scales: {
            r: { // 
              beginAtZero: true, // 0 ?
              max: 5,
            }
        },
        plugins: {
            legend: {
                display: false,
            },
        },
    }
  
    return (
        <TotalChartDiv>
            <Radar data={data} options={options} />
        </TotalChartDiv>
    )
  }

  // styled-components
const PosterDiv = styled.div`
    width: 700px;
    height: 180px;

    display: flex;
    justify-content: space-between;

    margin: 0 auto;
`;

const DivContainer = styled.div`
    width: 1200px;
    height: 570px;
`;

const TotalP = styled.p`
    font-size: 25px;
    font-weight: bold;

    margin: 50px auto;
`;

const ScoreContainer = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    width: 1000px;
    margin: auto;
`;

const StarDiv = styled.div`
    width: 50%;

    & p {
        background-color: #363636;

        font-size: 30px;
        font-weight: bold;

        width: 90%;
        height: 60px;

        padding: 7px 30px 0 30px;

        box-sizing: border-box;
        text-align: left;

        display: flex;
        justify-content: space-between;

        
        &:nth-child(2),
        :nth-child(3) {
            margin: 10px 0;
        }

        & span {
            color: yellow;
        }
    }
`;

const TotalChartDiv = styled.div`
    width: 470px;
    height: 250px;
  
    padding: 10px;
  
    background-color: white;
`;

const WordCloudP = styled.p`
    font-size: 30px;

    text-align: left;
    margin: 0 0 30px 100px;
`;